@import url(https://fonts.googleapis.com/css?family=Sacramento);
#root, body, html {
  height: 100%;
  width: 100%;
}

#app {
  position: relative;
  background: #fff;
  min-height: 100vh;
}

.app-content {
  padding: 30px 120px;
  background-repeat: no-repeat;
  background-position: 10% 0;
}

.ant-layout-header {
  -webkit-align-items: center;
          align-items: center;
  background-color: #f0f2f5;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.ant-layout-header h3 {
  margin: 0;
}

.ant-list-footer {
  text-align: center;
}

.ant-list-bordered .ant-list-header h4 {
  color: rgb(254, 127, 45);
  margin-bottom: 0;
  text-align: center;
}
.app-header {
  display: -webkit-flex;
  display: flex;
  background: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  padding: 0;
}

.app-header__container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.app-header__logo {
  display: inline-block;
  padding: 0 20px 10px;
}

.app-header__logo img {
  width: 36px;
}

.app-header__menu-section {
  padding-left: 2em;
}

.app-header__search-input {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding-right: 10px;
}

.app-header__search-input .ant-input-group {
  border: 0;
  width: 100%;
  max-width: 460px;
}

.app-header__search-input .ant-input-group input {
  border: 1px solid #f0f1f2;
  box-shadow: 0 2px 4px rgba(29, 34, 108, 0.1);
}

.app__affix-header {
  z-index: 99;
}
.area-field {
  width: 20%;
}
.phone-field {
  width: 80%;
}

.area-field, .phone-field {
  margin-bottom: 0;
}
.guests-list {
  background-color: #fff;
  margin: 0 auto 3em;
}
.ant-layout-content .login {
  margin: 2em auto;
  width: 35%;
}

.ant-layout-content .login .ant-form-item-control-input-content > button {
  margin-bottom: 1em;
  margin-left: 2em;
}

@media (max-width: 1200px) {
  .ant-layout-content .login {
    width: 50%;
  }
}

@media (max-width: 900px) {
  .ant-layout-content .login {
    width: 60%;
  }
}

.ant-layout-content .login .ant-form-item-control-input-content {
  text-align: right;
}
#chart {
  margin-top: 2em;
}

.app-content.timeline {
  min-width: 1000px;
}

.react-calendar-timeline {
  color: #4c4c4c;
}

.react-calendar-timeline .rct-scroll {
  border-right: 1px solid #eee;
}

.react-calendar-timeline .rct-calendar-header {
  border: 1px solid #eee;
  border-bottom: none;
}

.react-calendar-timeline .rct-calendar-header .days {
  border-top: 1px solid #eee;
}

.react-calendar-timeline .rct-dateHeader {
  background-color: #fff;
  border-bottom: none;
  border-left: none;
  border-right: 1px solid #eee;
  color: #4c4c4c;
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-even, .react-calendar-timeline .rct-horizontal-lines .rct-hl-odd {
  border-bottom: 1px solid #eee;
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row {
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.react-calendar-timeline .rct-sidebar {
  border-right: 1px solid #eee;
  box-shadow: 10px 0 10px -5px rgb(12 12 12 / 10%);
  font-weight: 700;
  text-align: center;
  z-index: 2;
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-odd, .react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-odd, .react-calendar-timeline .rct-header-root {
  background-color: #fff;
  border-bottom: 1px solid #d5d5d5;
}

.react-calendar-timeline .rct-item .rct-item-content {
  width: 100%;
}

.react-calendar-timeline .rct-vertical-lines .rct-vl {
  border-left: 1px solid #eee;
}

.react-calendar-timeline .rct-vertical-lines .rct-vl.rct-vl-first {
  border-left-width: 1px;
}

.react-calendar-timeline .rct-header-root .side-header {
  border-left: 1px solid #eee;
  border-top: 1px solid #eee;
}

.react-calendar-timeline .rct-items .reservation-item {
  border-radius: 4px;
  box-shadow: rgb(0 0 0 / 25%) 1px 1px 0px;
}

.rt-marker__label {
  position: absolute;
  left: 50%;
  display: table;
  min-width: 70px;
  height: 40px;
  padding: 0 10px;
  line-height: 1.1;
  text-align: center;
  background: currentColor;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 16px;
  font-weight: bold;
  z-index: 32;
}

.rt-marker__label::before {
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -6px;
  -webkit-transform: translateX(-1px);
          transform: translateX(-1px);
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid currentColor;
  content: ' ';
}

.rt-marker__content {
  display: table-cell;
  vertical-align: middle;
  white-space: nowrap;
  color: white;
}
.cancel-button {
  float: left
}

.roommate-list, .roommate-list>.ant-space-item:first-child {
  width: 100%
}
.suites-list {
  background-color: #fff;
  margin: 0 auto 3em;
}
